body[data-theme='dark'], .dark-bg, .dark-mode-texts {
  --bg:  #0E1019;
  --bg-opposite: #fcfdfe;
  --bg-2:rgba(255,255,255,0.01);
  --bg-3:#0E1019;
  --bg-4:#0E1019;
  --bg-5:#0E1019;
  --bg-6:#0E1019;
  --color-headings: #fff;
  --color-headings-opacity:rgba(255, 255, 255, 0.4);
