  outline: none !important;
  box-shadow: none !important;
}

.separator-line {
  width: 1px;
  height: 169px;
  border: 1px solid var(--border-color);
  margin: 0 auto;
  margin-bottom: 30px;
}

.strike-bottom {
  display: inline-block;
  position: relative;
  z-index: 1;
}

.strike-bottom::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 4px;
  left: 0;
  bottom: 5px;
  background: white;
  z-index: -1;
}

.strike-bottom.green::after {
  background: #68d585;
}

.strike-bottom.red::after {
  background: #f64b4b;
}

.strike-bottom.blue::after {
  background: #473bf0;
}

.strike-bottom.white::after {
  background: white;
}

.nice-select:active, .nice-select.open, .nice-select:focus {
  border-color: rgba(71, 59, 240, 0.22) !important;
}

.nice-select.rounded-8 {
  border-radius: 8px !important;
}

.nice-select.arrow-2:after {
  border-width: 5px;
  border-color: #000 transparent transparent transparent;
  border-style: solid;
  display: block;
  height: 5px;
  width: 5px;
  margin-top: -2.5px;
  transform-origin: 40% 23%;
  transform: rotate(0deg);
}

.nice-select.arrow-2.open:after {
  transform: rotate(180deg);
}

.nice-select.arrow-3:after {
  right: 16px;
  height: 8px;
  width: 8px;
  border-color: #161c2d;
}

.nice-select.arrow-3.open:after {
  transform: rotate(180deg);
}

.arrow-box-dropdown:after {
  border-width: 5px;
  border-color: #000 transparent transparent transparent;
  border-style: solid;
  display: block;
  height: 5px;
  width: 5px;
  margin-top: -2.5px;
  transform-origin: 40% 23%;
  transform: rotate(0deg);
  pointer-events: none;
  position: absolute;
  right: 12px;
  top: 50%;
  content: '';
}

.arrow-box-dropdown.open:after {
  transform: rotate(180deg);
}

.arrow-toggle:after {
  border-width: 5px;
  border-color: #000 transparent transparent transparent;
  border-style: solid;
  display: block;
  content: "";
  height: 5px;
  width: 5px;
  margin-top: -2.5px;
  transform-origin: 40% 23%;
  transform: rotate(0deg);
  pointer-events: none;
  position: absolute;
  right: 12px;
  top: 50%;
  transition: all .15s ease-in-out;
}

