.doc-topbar {
  background: #fff;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 800;
}

.doc-topbar .buy-button {
  display: none;
}

@media (min-width: 480px) {
  .doc-topbar .buy-button {
    display: block;
  }
}

.doc-topbar .mobile-toggle-btn {
  display: block;
  margin-top: 8px;
}

@media (min-width: 992px) {
  .doc-topbar .mobile-toggle-btn {
    display: none;
  }
}

.doc-topbar .navbar {
  z-index: 800;
}

.mobile-toggle-btn {
  background: none;
  border-width: 0;
  outline: none !important;
}

.mobile-toggle-btn .hamburger-box {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 24px;
}

.mobile-toggle-btn .hamburger-box .hamburger-inner {
  position: absolute;
  width: 40px;
  height: 4px;
  top: auto;
  bottom: 0;
  transition-property: transform;
  transition-delay: .13s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition-duration: .13s;
  border-radius: 4px;
  margin-top: -2px;
  background-color: #fff;
}

.mobile-toggle-btn .hamburger-box .hamburger-inner:after, .mobile-toggle-btn .hamburger-box .hamburger-inner:before {
  position: absolute;
  display: block;
  content: "";
  width: 40px;
  height: 4px;
  transition-timing-function: ease;
  transition-duration: .15s;
  transition-property: transform;
  border-radius: 4px;
  background-color: #fff;
}

.mobile-toggle-btn .hamburger-box .hamburger-inner:before {
  transition: top 0.12s cubic-bezier(0.33333, 0.66667, 0.66667, 1) 0.2s, transform 0.13s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  top: -10px;
}

.mobile-toggle-btn .hamburger-box .hamburger-inner:after {
  top: -20px;
  transition: top 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1) 0.2s, opacity 0.1s linear;
  bottom: -10px;
}

.mobile-toggle-btn[aria-expanded="true"] .hamburger-inner {
  transition-delay: .22s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  transform: translate3d(0, -10px, 0) rotate(-45deg);
}

.mobile-toggle-btn[aria-expanded="true"] .hamburger-inner:before {
  top: 0;
  transition: top 0.1s cubic-bezier(0.33333, 0, 0.66667, 0.33333) 0.16s, transform 0.13s cubic-bezier(0.215, 0.61, 0.355, 1) 0.25s;
  transform: rotate(-90deg);
}

.mobile-toggle-btn[aria-expanded="true"] .hamburger-inner:after {
  top: 0;
  transition: top 0.2s cubic-bezier(0.33333, 0, 0.66667, 0.33333), opacity 0.1s linear 0.22s;
  opacity: 0;
}

