.gr-abs-md{
  @include tablet{
    position: absolute;
  }
}

.gr-abs-lg {
  @include desktops {
    position: absolute!important;
  }
}

.gr-abs-lg-ly-center{
  @include desktops {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
}

.gr-abs-lg-ry-center{
  @include desktops {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }
}
.gr-abs-bx-center{
  @include desktops {
    position: absolute;
    bottom: 55px;
    right: 50%;
    transform: translateX(50%);
  }
}

.gr-abs-tl {
  position: absolute;
  top: 0;
  left: 0;
}
.gr-abs-tr {
  position: absolute;
  top: 0;
  right: 0;
}

.gr-abs-bl {
  position: absolute;
  bottom: 0;
  left: 0;
}
.gr-abs-br {
  position: absolute;
  bottom: 0;
  right: 0;
}
.gr-abs-cr {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}
.gr-abs-md-cr {
  @include tablet {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }
}
.gr-abs-center{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.gr-abs-br-custom {
  position: absolute;
  bottom: -50px;
  right: -36px;
}

.gr-abs-bl-custom {
  position: absolute;
  bottom: -50px;
  left: -36px;
  z-index: -1;
}

.gr-abs-bl-custom-2 {
  position: absolute;
  bottom: -5px;
  left: -70px;
  z-index: -1;
}
.gr-abs-bl-custom-3 {
  position: absolute;
  bottom: 0;
  left: -16%;
}

.gr-abs-tl-custom {
  position: absolute;
  top: -6px;
  left: 45px;
}

.gr-abs-tl-custom-2 {
  position: absolute;
  top: 40px;
  left: 52px;
  z-index: -1;
  @include desktops {
    top: 24px;
    left: 35px;
  }
  @include large-desktops {
    top: 40px;
    left: 52px;
  }
}

.gr-abs-tr-custom {
  position: absolute;
  top: -32px;
  right: -38px;
}

.gr-abs-tr-custom-2 {
  position: absolute;
  top: 26%;
  right: 15%;
  z-index: -1;
}

.gr-z-index-n1 {
  z-index: -1;
}
.gr-z-index-1 {
  z-index: 1;
}
.z-index-99{
  z-index: 99;
}
.z-index-super{
  z-index: 9999;
}
